* {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "source-sans-pro","Lato",sans-serif; }

#main-nav {
  background: #6799FF;
  color: white;
  text-align: center;
  line-height: 2;
  padding: 0 1rem; }
  #main-nav ul {
    margin: 0;
    padding: 0; }
  #main-nav > * {
    display: inline-block; }
    #main-nav > *:first-child {
      float: left; }
    #main-nav > *:last-child {
      float: right; }
  #main-nav > .logo {
    margin-left: auto;
    margin-right: auto; }
  #main-nav ul {
    list-style-type: none; }
    #main-nav ul li {
      display: inline-block; }
  #main-nav a {
    color: currentColor; }

#toc {
  background: green;
  height: 300vh;
  width: 20vw; }
